import { getTargetDriveFromProfileId, BuiltInProfiles } from '@youfoundation/dotyoucore-js';
import { useState, useRef } from 'react';
import useConnectionDetails from '../../../../../hooks/connections/useConnectionDetails';
import { useIntersection } from '../../../../../hooks/intersection/useIntersection';
import useSiteData from '../../../../../hooks/siteData/useSiteData';
import Image from '../../../../Image/Image';
import Person from '../../../../ui/Icons/Person/Person';

interface ImageProps {
  className?: string;
  size?: 'sm' | 'md' | 'custom';
}

interface ConnectionImageProps extends ImageProps {
  dotYouId?: string;
}

const AuthorImage = ({ dotYouId, ...props }: ConnectionImageProps) => {
  const ownerHost = window.location.hostname;

  if (dotYouId && ownerHost !== dotYouId) {
    return (
      <a href={`https://${dotYouId}`}>
        <ConnectionImage {...props} dotYouId={dotYouId} />
      </a>
    );
  } else {
    return <OwnerImage {...props} />;
  }
};

export const OwnerImage = ({ className, size }: ImageProps) => {
  const { owner } = useSiteData().data ?? {};

  const targetDrive = getTargetDriveFromProfileId(BuiltInProfiles.StandardProfileId);

  return (
    <Image
      fileId={owner?.profileImageId}
      targetDrive={targetDrive}
      className={`${
        size === 'sm' ? 'h-[3rem] w-[3rem]' : size === 'md' ? 'h-[5rem] w-[5rem]' : ''
      } rounded-full ${className ?? ''}`}
      fit="cover"
    />
  );
};

export const ConnectionImage = ({ dotYouId, className, size }: ConnectionImageProps) => {
  const [isInView, setIsInView] = useState(false);
  const wrapperRef = useRef<HTMLDivElement>(null);

  const { data: connectionDetails } = useConnectionDetails({
    dotYouId: isInView ? dotYouId : undefined,
  }).fetch;

  useIntersection(wrapperRef, () => {
    setIsInView(true);
  });

  return (
    <div ref={wrapperRef}>
      {connectionDetails?.profileImage?.url ? (
        <img
          src={connectionDetails.profileImage.url}
          width={connectionDetails.profileImage.naturalSize.width}
          height={connectionDetails.profileImage.naturalSize.height}
          className={`${
            size === 'sm' ? 'h-[3rem] w-[3rem]' : size === 'md' ? 'h-[5rem] w-[5rem]' : ''
          } rounded-full ${className ?? ''}`}
        />
      ) : (
        <Person className="h-4 w-4" />
      )}
    </div>
  );
};

export default AuthorImage;
