import { useQuery, useQueryClient } from '@tanstack/react-query';
import {
  BlogConfig,
  PublicClient,
  HomePageConfig,
  TargetDrive,
  ThumbSize,
} from '@youfoundation/dotyoucore-js';
import useAuth from '../auth/useAuth';

const defaultDrive: TargetDrive = {
  alias: HomePageConfig.BlogMainContentDriveId.toString(),
  type: BlogConfig.DriveType.toString(),
};

const useImage = (imageFileId: string | undefined, imageDrive?: TargetDrive, size?: ThumbSize) => {
  const { getSharedSecret, getApiType } = useAuth();
  const queryClient = useQueryClient();

  const fetchImageData = async (
    imageFileId: string | undefined,
    imageDrive?: TargetDrive,
    size?: ThumbSize
  ) => {
    if (imageFileId === undefined || imageFileId === '') {
      return;
    }

    const client = new PublicClient({ api: getApiType(), sharedSecret: getSharedSecret() });
    return await client.mediaProvider.getDecryptedImageUrl(
      imageDrive ?? defaultDrive,
      imageFileId,
      size
    );
  };

  return {
    fetch: useQuery(
      ['image', imageFileId, imageDrive, size],
      () => fetchImageData(imageFileId, imageDrive, size),
      {
        refetchOnMount: false,
        refetchOnWindowFocus: false,
        staleTime: Infinity,
        enabled: !!imageFileId && imageFileId !== '',
      }
    ),
    testCache: (imageFileId: string, imageDrive: TargetDrive) => {
      const cache = queryClient.getQueryData(['image', imageFileId, imageDrive]);
      return !!cache;
    },
  };
};

export default useImage;
