import { AxiosError } from 'axios';
import { ApiType, ProviderBase } from '@youfoundation/dotyoucore-js';

export class OwnerProviderBase extends ProviderBase {
  constructor(sharedSecret: Uint8Array | undefined | null) {
    super({ api: ApiType.Owner, sharedSecret: sharedSecret ?? undefined });
  }

  protected getSharedSecret(): Uint8Array {
    const internalSharedSecret = super.getSharedSecret();
    if (!internalSharedSecret) {
      throw new Error('Shared Secret not set for OwnerProvider');
    }
    return internalSharedSecret;
  }

  //Returns the endpoint for the identity
  protected getEndpoint(): string {
    return 'https://' + window.location.hostname + '/api/owner/v1';
  }

  protected handleErrorResponse(error: AxiosError) {
    throw error;
  }
}
