import { FC, ReactNode } from 'react';
import Header from './Header/Header';
import Footer from './Footer/Footer';
import useSiteData from '../../../hooks/siteData/useSiteData';
import { t } from '../../../helpers/i18n/dictionary';
import useTheme from '../../../hooks/theme/useTheme';

interface LayoutProps {
  children?: ReactNode;
}

const Layout: FC<LayoutProps> = ({ children }) => {
  const { data: siteData, isFetched: siteDataFetched } = useSiteData();
  const { colors } = useTheme();

  if (
    siteData &&
    siteDataFetched &&
    (siteData.home?.template === '0' || !siteData.home?.template)
  ) {
    return (
      <div className="flex min-h-screen">
        <span className="m-auto">{t('This is a registered dotyou identity')}</span>
      </div>
    );
  } else if (siteDataFetched) {
    return (
      <>
        <style type="text/css">
          {`:root {
            ${Object.keys(colors.light)
              .map((key) => `--color-${key}: ${colors.light[key]};`)
              .join('')}
          }`}
          {`html.dark {
            ${Object.keys(colors.dark)
              .map((key) => `--color-${key}: ${colors.dark[key]};`)
              .join('')}
          }`}
        </style>
        <div className={`flex min-h-screen flex-col bg-background text-foreground`}>
          <Header />
          {children}
          <Footer className="mt-auto" />
        </div>
      </>
    );
  }

  return null;
};

export default Layout;
