import { useQuery } from '@tanstack/react-query';
import {
  AttributeDefinitions,
  BuiltInProfiles,
  PublicClient,
  HomePageAttributes,
  Attribute,
  HomePageFields,
  HomePageThemeFields,
  AttributeFile,
} from '@youfoundation/dotyoucore-js';
import useAuth from '../auth/useAuth';

type socialInfo = { type: string; username: string };

type SiteData = {
  owner: {
    firstName?: string;
    surName?: string;
    profileImageId?: string;
  };
  social: { type: string; username: string }[];
  home: {
    template?: string;
    templateSettings?: unknown;
    tagLine?: string;
    leadText?: string;
    headerImageFileId?: string;
  };
};

const useSiteData = () => {
  const { getSharedSecret, getApiType } = useAuth();

  const fetchData: () => Promise<SiteData> = async () => {
    const client = new PublicClient({ api: getApiType(), sharedSecret: getSharedSecret() });
    const fileData = await client.fileReadOnlyProvider.GetFile('sitedata.json');

    const getOwnerData = async () => {
      // File based response if available
      if (fileData.has('name') && fileData.has('photo')) {
        const nameAttr = fileData.get('name')?.[0]?.payload as Attribute;
        const photoAttr = fileData.get('photo')?.[0]?.payload as Attribute;

        if (nameAttr && photoAttr) {
          return {
            firstName: nameAttr?.data.givenName,
            surName: nameAttr?.data.surname,
            profileImageId: photoAttr?.data.profileImageId,
          };
        }
      }

      const nameAndPhotoAttr = getHighestPrioAttributesFromMultiTypes(
        await client.profileDataProvider.getAttributeVersions(
          BuiltInProfiles.StandardProfileId,
          BuiltInProfiles.PersonalInfoSectionId,
          [AttributeDefinitions.Name.type, AttributeDefinitions.Photo.type]
        )
      );

      const nameAttr = nameAndPhotoAttr?.find(
        (attr) => attr.type === AttributeDefinitions.Name.type
      );
      const photoAttr = nameAndPhotoAttr?.find(
        (attr) => attr.type === AttributeDefinitions.Photo.type
      );

      return {
        firstName: nameAttr?.data.givenName,
        surName: nameAttr?.data.surname,
        profileImageId: photoAttr?.data.profileImageId,
      };
    };

    const getSocialData = async () => {
      if (fileData.has('socials')) {
        const fileBasedResponse = fileData
          .get('socials')
          ?.sort((a, b) => (b?.payload.priority ?? 0) - (a?.payload.priority ?? 0))
          ?.map((entry) => {
            const value = Object.values(entry.payload.data)?.[0];

            return {
              type: Object.keys(entry.payload.data)?.[0],
              username: typeof value === 'string' ? value : '',
            };
          });

        if (fileBasedResponse?.length) {
          return fileBasedResponse;
        }
      }

      const socialAttributeVersions = await client.profileDataProvider.getAttributeVersions(
        BuiltInProfiles.StandardProfileId,
        BuiltInProfiles.ExternalLinksSectionId,
        [
          AttributeDefinitions.TwitterUsername.type,
          AttributeDefinitions.InstagramUsername.type,
          AttributeDefinitions.FacebookUsername.type,
          AttributeDefinitions.LinkedinUsername.type,
          AttributeDefinitions.TiktokUsername.type,
          AttributeDefinitions.DotYouIdentity.type,
        ]
      );

      const socialAttributes = getHighestPrioAttributesFromMultiTypes(socialAttributeVersions);

      return socialAttributes
        ?.map((attr) => {
          const value = Object.values(attr?.data)?.[0];

          return {
            type: Object.keys(attr?.data)?.[0],
            username: typeof value === 'string' ? value : '',
          };
        })
        .filter((attr) => attr !== undefined) as socialInfo[];
    };

    const getHomeData = async () => {
      // File based response if available
      if (fileData.has('home') && fileData.has('theme')) {
        const homeAttribute = fileData.get('home')?.[0]?.payload as Attribute;
        const themeAttribute = fileData.get('theme')?.[0]?.payload as Attribute;

        if (homeAttribute && themeAttribute) {
          const tagLine = homeAttribute?.data[HomePageFields.TagLineId];
          const leadText = homeAttribute?.data[HomePageFields.LeadTextId];
          const headerImageFileId = homeAttribute?.data[HomePageFields.HeaderImageId];

          const homePageTheme = themeAttribute?.data[HomePageThemeFields.ThemeId];

          return {
            template: homePageTheme,
            templateSettings: themeAttribute?.data,
            tagLine: tagLine,
            leadText: leadText,
            headerImageFileId: headerImageFileId,
          };
        }
      }

      // Full response
      const homeAndThemeAttr = getHighestPrioAttributesFromMultiTypes(
        await client.homePageReadOnlyProvider.getAttributeVersions([
          HomePageAttributes.HomePage,
          HomePageAttributes.Theme,
        ])
      );

      const homeAttribute = homeAndThemeAttr?.find(
        (attr) => attr.type === HomePageAttributes.HomePage
      );
      const themeAttribute = homeAndThemeAttr?.find(
        (attr) => attr.type === HomePageAttributes.Theme
      );

      // Page Config
      const tagLine = homeAttribute?.data[HomePageFields.TagLineId];
      const leadText = homeAttribute?.data[HomePageFields.LeadTextId];
      const headerImageFileId = homeAttribute?.data[HomePageFields.HeaderImageId];

      const homePageTheme = themeAttribute?.data[HomePageThemeFields.ThemeId];

      return {
        template: homePageTheme,
        templateSettings: themeAttribute?.data,
        tagLine: tagLine,
        leadText: leadText,
        headerImageFileId: headerImageFileId,
      };
    };

    const newData = {
      owner: await getOwnerData(),
      social: await getSocialData(),
      home: await getHomeData(),
    };

    return newData;
  };

  return useQuery(['siteData'], fetchData, {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
    staleTime: Infinity,
    onError: (er) => {
      console.error(er);
    },
  });
};

export default useSiteData;

const getHighestPrioAttributesFromMultiTypes = (attributes?: AttributeFile[]) => {
  if (attributes) {
    return attributes?.reduce((highestPrioArr, attr) => {
      const highAttr = highestPrioArr.find((highAttr) => highAttr.type === attr.type);
      if (!attr.data) {
        return highestPrioArr;
      }

      if (highAttr) {
        if (highAttr.priority < attr.priority) {
          return highestPrioArr;
        } else {
          return [...highestPrioArr.filter((highPrio) => highPrio.type !== attr.type), attr];
        }
      } else {
        return [...highestPrioArr, attr];
      }
    }, [] as AttributeFile[]);
  }
};
