import { useQuery } from '@tanstack/react-query';
import {
  ApiType,
  // AttributeDefinitions,
  // BuiltInProfiles,
  DataUtil,
  PublicClient,
} from '@youfoundation/dotyoucore-js';

const useConnectionDetails = ({ dotYouId }: { dotYouId?: string }) => {
  const client = new PublicClient({ api: ApiType.YouAuth, root: dotYouId });

  const fetchSingle = async ({ dotYouId }: { dotYouId?: string }) => {
    if (!dotYouId) {
      return;
    }

    // const bestNameAttr = await client.profileDataProvider.getBestAttributeVersion(
    //   BuiltInProfiles.StandardProfileId.toString(),
    //   BuiltInProfiles.PersonalInfoSectionId.toString(),
    //   AttributeDefinitions.Name.type.toString()
    // );
    // console.log(bestNameAttr);

    const rawData = await client.fileReadOnlyProvider.GetFile('public.json');

    const nameAttr = rawData.get('name')?.[0];
    const photoRefAttr = rawData.get('photo')?.[0];
    const photoAttr = rawData.get(photoRefAttr?.payload?.data?.profileImageId)?.[0];

    const previewThumbnail = photoAttr?.additionalThumbnails?.reduce(
      (prevVal, curValue) => {
        if (prevVal.pixelWidth < curValue.pixelWidth && curValue.pixelWidth <= 250) {
          return curValue;
        }
        return prevVal;
      },
      { ...photoAttr.header.fileMetadata.appData.previewThumbnail, pixelWidth: 20, pixelHeight: 20 }
    );

    const buffer = previewThumbnail?.content
      ? DataUtil.base64ToUint8Array(previewThumbnail.content)
      : undefined;
    const url = buffer ? window.URL.createObjectURL(new Blob([buffer])) : undefined;

    const returnVal = {
      name: {
        ...nameAttr?.payload.data,
      },
      profileImage: {
        naturalSize: { width: previewThumbnail?.pixelWidth, height: previewThumbnail?.pixelHeight },
        contentType: previewThumbnail?.contentType,
        url,
      },
    };

    return returnVal;
  };

  return {
    fetch: useQuery(['connectionDetails', dotYouId], () => fetchSingle({ dotYouId }), {
      refetchOnWindowFocus: false,
      enabled: !!dotYouId,
    }),
  };
};

export default useConnectionDetails;
