import { DataUtil, EmbeddedThumb, TargetDrive, ThumbSize } from '@youfoundation/dotyoucore-js';
import { useEffect, useRef, useState } from 'react';
import useImage from '../../hooks/image/useImage';
import useTinyThumb from '../../hooks/image/useTinyThumb';
import { useIntersection } from '../../hooks/intersection/useIntersection';
import LoadingParagraph from '../ui/LoadingParagraph/LoadingParagraph';

export interface ImageProps {
  targetDrive: TargetDrive;
  fileId: string | undefined;
  fit?: 'cover' | 'contain';
  className?: string;
  alt?: string;
  title?: string;
  previewThumbnail?: EmbeddedThumb;
}

const getEmbeddedThumbUrl = (previewThumbnail: EmbeddedThumb) => {
  const buffer = DataUtil.base64ToUint8Array(previewThumbnail.content);
  return window.URL.createObjectURL(new Blob([buffer], { type: previewThumbnail.contentType }));
};

// TODO: merge with ExternalImage;
const Image = ({
  targetDrive,
  fileId,
  fit,
  className,
  alt,
  title,
  previewThumbnail,
}: ImageProps) => {
  // // Temp disabled previewThumbnail usage
  // previewThumbnail = undefined as any;
  const [isInView, setIsInView] = useState(false);
  const [loadSize, setLoadSize] = useState<ThumbSize | 'full' | undefined>(undefined);
  const imgRef = useRef<HTMLImageElement>(null);
  const { data: thumbMeta } = useTinyThumb(
    isInView && !previewThumbnail ? fileId : undefined,
    targetDrive
  );
  const embeddedThumbUrl = previewThumbnail ? getEmbeddedThumbUrl(previewThumbnail) : undefined;
  const {
    fetch: { data: imageUrl },
    testCache,
  } = useImage(
    loadSize !== undefined ? fileId : undefined,
    targetDrive,
    loadSize !== 'full' ? loadSize : undefined
  );

  useIntersection(imgRef, () => {
    if ((fileId && testCache(fileId, targetDrive)) || previewThumbnail) {
      findLoadOptimalSize();
    }
    setIsInView(true);
  });

  useEffect(() => {
    if ((thumbMeta && thumbMeta.url?.length) || previewThumbnail) {
      // Set to load full, with timeout to allow other tinies to load
      setTimeout(() => {
        findLoadOptimalSize();
      }, 100);
    }
  }, [thumbMeta, previewThumbnail]);

  const findLoadOptimalSize = () => {
    if (!imgRef.current || (!thumbMeta?.sizes?.length && !previewThumbnail)) {
      setLoadSize('full');
      return;
    }

    const targetWidth = imgRef.current?.clientWidth;
    const targetHeight = imgRef.current?.clientHeight;

    if (!thumbMeta?.sizes?.length || !imgRef.current) {
      setLoadSize({
        contentType: previewThumbnail?.contentType ?? 'image/webp',
        pixelWidth: targetWidth,
        pixelHeight: targetHeight,
      });
      return;
    }

    const matchingSize = thumbMeta.sizes.find((size) => {
      return targetWidth < size.pixelWidth && targetHeight < size.pixelHeight;
    });

    setLoadSize(matchingSize ?? 'full');
  };

  const state: 'idle' | 'loading-tiny' | 'tiny' | 'loading-full' | 'loaded' = loadSize
    ? imageUrl
      ? 'loaded'
      : 'loading-full'
    : isInView
    ? thumbMeta && thumbMeta.url?.length
      ? 'tiny'
      : 'loading-tiny'
    : 'idle';

  return (
    <figure
      className={`${
        className?.indexOf('absolute') !== -1 ? '' : 'relative'
      } overflow-hidden ${className}`}
    >
      {state !== 'loading-tiny' ? (
        <img
          src={state === 'loaded' ? imageUrl : thumbMeta?.url ?? embeddedThumbUrl}
          alt={isInView && thumbMeta?.url ? alt : ' '}
          className={`${state !== 'loaded' ? 'blur-[50px]' : ''} ${
            fit === 'cover'
              ? 'h-full w-full object-cover'
              : fit === 'contain'
              ? 'm-auto max-h-full max-w-full object-contain'
              : 'h-auto max-h-[inherit] w-full'
          }`}
          title={
            title ||
            `${fileId}@${
              loadSize !== undefined && loadSize !== 'full'
                ? `${loadSize.pixelWidth}x${loadSize.pixelHeight}`
                : loadSize
            }`
          }
          ref={imgRef}
          width={thumbMeta?.naturalSize?.width ?? previewThumbnail?.pixelWidth}
          height={thumbMeta?.naturalSize?.height ?? previewThumbnail?.pixelHeight}
        />
      ) : (
        <LoadingParagraph className="aspect-square h-full w-full" />
      )}
    </figure>
  );
};

export default Image;
