import { lazy, ReactNode, Suspense } from 'react';
import { BrowserRouter as Router, Routes, Route, Outlet, Navigate } from 'react-router-dom';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import Layout from '../components/ui/Layout/Layout';

import { QueryClient, QueryClientProvider } from '@tanstack/react-query';

import './App.css';
import { ErrorBoundary } from '../components/ui/Layout/ErrorBoundary/ErrorBoundary';
import useAuth from '../hooks/auth/useAuth';

const NotFound = lazy(() => import('../templates/NotFound/NotFound'));
const Home = lazy(() => import('../templates/Home/Home'));
const BlogOverview = lazy(() => import('../templates/Blog/Overview/BlogOverview'));
const BlogDetail = lazy(() => import('../templates/Blog/Detail/BlogDetail'));
const BlogImageDetail = lazy(() => import('../templates/Blog/Detail/BlogImageDetail'));
const LinksPage = lazy(() => import('../templates/LinksPage/LinksPage'));
const YouAuthFinalizer = lazy(() => import('../templates/YouAuthFinalizer/YouAuthFinalizer'));

const SocialFeed = lazy(() => import('../templates/SocialFeed/SocialFeed'));
const PostComposerPage = lazy(() => import('../templates/SocialFeed/PostComposerPage'));

const queryClient = new QueryClient();

function App() {
  return (
    <HelmetProvider>
      <Helmet>
        <meta name="v" content={process.env.REACT_APP_VERSION} />
      </Helmet>
      <QueryClientProvider client={queryClient}>
        <Router>
          <Suspense fallback={<Layout />}>
            <Routes>
              <Route
                path="/home"
                element={
                  <Layout>
                    <Suspense>
                      <ErrorBoundary>
                        <Outlet />
                      </ErrorBoundary>
                    </Suspense>
                  </Layout>
                }
              >
                <Route index={true} element={<Home />} />

                <Route
                  path="feed"
                  element={
                    <RootRoute>
                      <SocialFeed />
                    </RootRoute>
                  }
                />
                <Route
                  path="feed/new"
                  element={
                    <RootRoute>
                      <PostComposerPage />
                    </RootRoute>
                  }
                />
                <Route
                  path="feed/edit/:channelKey/:postKey"
                  element={
                    <RootRoute>
                      <PostComposerPage />
                    </RootRoute>
                  }
                />
                <Route path="about" element={<Home tab="about" />} />
                <Route path="links" element={<Home tab="links" />} />
                <Route path="posts" element={<Home tab="posts" />} />
                <Route path="connections" element={<Home tab="connections" />} />

                {/* <Route path="feed" element={<BlogRecents />} /> */}
                <Route path="posts/:channelKey" element={<BlogOverview />} />
                <Route path="posts/:channelKey/:postKey" element={<BlogDetail />} />
                <Route
                  path="posts/:channelKey/:postKey/:attachmentKey"
                  element={<BlogImageDetail />}
                />

                <Route path="linked" element={<LinksPage />} />
                <Route path="youauth/finalize" element={<YouAuthFinalizer />} />

                {/* <Route path="channels" element={<BlogOverview />} /> */}
                {/* <Route path="connections" element={<Connections />} /> */}
              </Route>
              <Route
                path="*"
                element={
                  <Layout>
                    <NotFound />
                  </Layout>
                }
              />
            </Routes>
          </Suspense>
        </Router>
      </QueryClientProvider>
    </HelmetProvider>
  );
}

const RootRoute = ({ children }: { children: ReactNode }) => {
  const { isAuthenticated, isOwner } = useAuth();

  if (!isAuthenticated || !isOwner) {
    console.debug('[NOT AUTHENTICATED]: Redirect to home');

    return <Navigate to={`/home`} />;
  }

  return <>{children}</>;
};

export default App;
