import useSiteData from '../../../../../hooks/siteData/useSiteData';

const AuthorName = ({ dotYouId }: { dotYouId?: string }) => {
  if (!dotYouId && dotYouId !== window.location.hostname) {
    return <OwnerName />;
  }
  return (
    <a href={`https://${dotYouId}`} className="hover:underline">
      {dotYouId}
    </a>
  );
};

export const OwnerName = () => {
  const { owner } = useSiteData().data ?? {};

  return <>{`${owner?.firstName ?? ''} ${owner?.surName ?? ''}`}</>;
};

export default AuthorName;
